import React, { useState } from "react"
import Button from "react-bulma-components/esm/components/button"
import Notification from "react-bulma-components/esm/components/notification"
import Tag from "react-bulma-components/esm/components/tag"
import Obfuscate from "react-obfuscate"

const ConsentContainer = ({ children }) => {
  const [state, setState] = useState({ clicked: false })
  const consent = () => {
    setState(true)
  }
  return (
    <>
      {state.clicked === false ? (
        <Notification className={"is-size-7"}>
          <Button
            color="success"
            className="is-fullwidth"
            onClick={consent}
            style={{
              height: "auto",
              whiteSpace: "normal",
              marginBottom: "1rem",
            }}
          >
            Yeah, ich will auch den Newsletter!
          </Button>
          Das Newsletter Widget ldt Daten von Mailjet, Cloudflare und Google
          Fonts. Sonst schreib einfach eine Email an{" "}
          <Obfuscate email="hello@ivos-ecotainment-newsletter.info" />{" "}
          <Tag color="warning">DSGVO</Tag>
        </Notification>
      ) : (
        { ...children }
      )}
    </>
  )
}

export default ConsentContainer
